.container {
  display: block;
  margin: 0 auto;
  max-width: 70em;
  padding: 0 3rem;
  @include clearfix;
}

.header {
  background: $color-white;
  padding: 3rem 0;
  margin-bottom: 1rem;
}

.logo {
  float: left;
  font-weight: 600;
  margin: 0;
  line-height: 1;
}

.nav {
  float: right;

  a {
    @extend .logo;
  }
}

.content {
  background: $color-white;
  padding: 2rem 0 0;
  margin-bottom: 31.24rem;
  position: relative;
  top: 0;
  left: 0;
  z-index: 1;

  @media (min-width: $breakpoint-medium) {
    padding: 1rem 0;
    margin-bottom: 23.42rem;
  }
}

.intro,
.projects {
  padding: 0 0 5rem;
}

.intro {
  @media (min-width: $breakpoint-medium) {
    p {
      font-size: 1.5rem;
      max-width: 66.66666%;
    }
  }
}

.projects-list {
  @media (min-width: $breakpoint-small) {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    @include clearfix;

    li {
      float: left;
      width: 50%;
    }

    a {
      padding: 0 1.5rem;
    }
  }

  @media (min-width: $breakpoint-medium) {
    li {
      width: 24.6666%;
    }
  }

  li {
    margin-bottom: 3rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    display: block;

    &:hover {
      .img-wrapper {
        &:before {
          opacity: 1;
        }
        &:after {
          opacity: 0.45;
        }
      }
    }
  }

  .img-wrapper {
    position: relative;

    &:before {
      content: 'View More';
      color: $color-white;
      @extend .h2;
      font-weight: 800;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -0.5rem;
      margin-left: -4.4rem;
      opacity: 0;
      -webkit-transition: all 0.5s ease-in-out;
      transition: all 0.5s ease-in-out;
      -webkit-transition-delay: 0.25s;
      transition-delay: 0.25s;
      z-index: 2;
    }

    &:after {
      background: #000;
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0;
      -webkit-transition: all 0.5s ease-in-out;
      transition: all 0.5s ease-in-out;
      z-index: 1;
    }
  }

  img {
    width: 100%;
  }

  span {
    margin: 1rem 0 0.5rem;
  }
}

.project {
  // padding-top: 3em;
  padding-bottom: 5em;

  .cover {
    max-width: 350px;
    margin-bottom: 3em;

    img {
      width: 100%;
    }
  }

  .block {
    display: flex;
    margin: 1em 0;
  }

  a {
    color: $color-blue;
    text-decoration: none;
  }

  h3 {
    font-size: 1.5em;
    font-weight: 700;
  }
}

.project-navigation {
  background: $color-black;
  padding: 5rem 0;
  margin-bottom: 1em;

  a {
    color: $color-white;
    display: inline-block;

    &:hover {
      h2:after {
        visibility: visible;
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
      }

      &.prev {
        span {
          left: 7px;
        }
      }

      span {
        &:after {
          left: 7px;
        }

        &:before {
          left: -7px;
        }
      }
    }

    &:nth-of-type(1),
    &:nth-of-type(2) {
      display: table;
      margin: 0 auto;
      text-align: center;

      @media (min-width: $breakpoint-xsmall) {
        display: inline-block;
        margin: 0;
        text-align: left;
      }
    }

    &:nth-of-type(1) {
      @media (min-width: $breakpoint-xsmall) {
        float: left;
      }
    }

    &:nth-of-type(2) {
      margin-top: 3rem;

      @media (min-width: $breakpoint-xsmall) {
        float: right;
        margin-top: 0;
      }
    }

    &.next {
      span {
        &:after {
          content: '\02192';
        }
      }
    }

    &.prev {
      span {
        position: relative;
        left: 0;
        -webkit-transition: all 0.3s ease-in-out 0s;
        transition: all 0.3s ease-in-out 0s;

        &:before {
          content: '\02190';
        }
      }
    }
  }

  span {
    &:after,
    &:before {
      position: relative;
      left: 0;
      -webkit-transition: all 0.3s ease-in-out 0s;
      transition: all 0.3s ease-in-out 0s;
    }
  }

  h2 {
    display: inline-block;
    font-size: 1.8rem;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      bottom: -4px;
      left: 0;
      background-color: $color-white;
      visibility: hidden;
      -webkit-transform: scaleX(0);
      transform: scaleX(0);
      -webkit-transition: all 0.3s ease-in-out 0s;
      transition: all 0.3s ease-in-out 0s;
    }
  }
}

.footer {
  background: $color-alabaster;
  bottom: 0;
  left: 0;
  padding: 5rem 0;
  position: fixed;
  right: 0;
  z-index: -1;

  a {
    color: $color-blue;
  }
}

.contact,
.follow {
  @media (min-width: $breakpoint-medium) {
    float: left;
    margin-bottom: 0;
    width: 43.33333%;
  }
}

.contact,
.follow {
  margin-bottom: 3rem;
}

@media screen and (max-width: 700px) {
  p,
  .h2,
  a {
    // font-size: 80%;
  }

  .project-navigation {
    display: none;
  }

  .content,
  .footer {
    font-size: 80%;
  }
}
